.wrapp{display:flex;flex-direction:column;gap:1.25rem}.list{display:flex;flex-direction:column;gap:10px;padding-left:1.25rem}.item{display:flex;flex-direction:column;gap:7px}.item:not(:last-child){padding-bottom:15px;border-bottom:1px solid var(--blue-400-01)}.main{position:relative;display:flex;flex-direction:column;gap:.9375rem;border-bottom:1px solid var(--grey-100);padding:.625rem}.main_content{line-height:1.6;font-size:1rem;position:relative;height:30px;overflow:hidden;transition:height .2s}.main_content::before{position:absolute;content:"";bottom:0;left:0;width:100%;height:20%;background:linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #fff 90%)}.main_content.opened::before{display:none}.row{display:flex;justify-content:space-between;align-items:center;gap:.625rem}.row h3{font-weight:600;font-size:1.1rem}.row p{display:flex;align-items:center;color:var(--grey-400);font-size:1rem}.bottom{display:flex;align-items:center;gap:.9375rem}.link{position:relative;display:block;color:var(--blue-400)}.link::before{position:absolute;content:"•";top:50%;right:-12px;transform:translateY(-50%);color:var(--grey-400)}.toggler{position:absolute;z-index:3;opacity:.4;font-size:1.32rem;bottom:-7px;right:.625rem}